import React, { memo } from 'react';

import { useResponsive, usePrint } from '../../hooks';

import {
  LikeIcon,
  LikeFilledIcon,
  ShareIcon,
  ClipIcon,
  SaveIcon,
  ReportIcon,
  ReportFilledIcon,
  MoreIcon,
} from '../../assets';

import { StyledPlayerHeader } from './styles';

const PlayerHeader = memo(({ className, selectVideo }) => {
  const { isMobile } = useResponsive();
  const { printView, printLike, printDate } = usePrint();

  return (
    <StyledPlayerHeader className={className}>
      <h1 className="title">{selectVideo.snippet.title}</h1>

      <div className="player-statistics">
        <div className="player-view">
          <span>
             <strong>{printView(selectVideo)}</strong>
          </span>
          <span>{printDate(selectVideo)}</span>
        </div>

        <div className="button-group">
          <button type="button" aria-label=" ">
            <LikeIcon aria-hidden="true" />
            {printLike(selectVideo)}
            <span className="visually-hidden"></span>
          </button>

          <button type="button" aria-label="   ">
            <LikeIcon aria-hidden="true" />
            
          </button>

          <button type="button">
            <ShareIcon aria-hidden="true" />
            
          </button>

          {!isMobile && (
            <button type="button">
              <ClipIcon aria-hidden="true" />
              
            </button>
          )}

          <button type="button">
            <SaveIcon aria-hidden="true" />
            
          </button>

          {isMobile && (
            <button type="button">
              <ReportIcon aria-hidden="true" />
              
            </button>
          )}

          {!isMobile && (
            <button type="button" aria-label="">
              <MoreIcon aria-hidden="true" />
            </button>
          )}
        </div>
      </div>
    </StyledPlayerHeader>
  );
});

export default PlayerHeader;
